.Youtube {
	article {
		border-top: 2px solid #888;
		padding: 25px 0px;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		h3 {
			width: 70%;
			font: bold 30px/1 "work sans";
			color: #000;
		}
		.txt {
			width: 38%;
			display: flex;
			flex-wrap: wrap;
			align-content: space-between;
			p {
				font: 20px/1.2 "arial";
				color: #999;
				// filter: saturate(0);
			}
			span {
				font: 25px/1 "arial";
				color: orange;
			}
		}
		.pic {
			width: 32%;
			height: 200px;
			background: #000;
			filter: saturate(0);
			transition: 0.5s;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
			&:hover {
				filter: saturate(1.2);
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	.Youtube {
		article {
			h3 {
				width: 48%;
			}
			.txt {
				width: 50%;
			}
			.pic {
				width: 100%;
				margin-top: 50px;
				margin-bottom: 80px;
			}
		}
	}
}

@media screen and (max-width: $mobile) {
	.Youtube {
		article {
			h3 {
				width: 100%;
				margin-bottom: 20px;
			}
			.txt {
				width: 100%;
			}
			.pic {
				height: 120px;
				margin-bottom: 0px;
			}
		}
	}
}
